.map-container {
    position: relative;
    width: 100%;
    background: rgba(15, 20, 40, 0.7);
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 0 20px rgba(80, 200, 255, 0.3);
    border: 1px solid rgba(80, 200, 255, 0.2);
    overflow: visible;
  }
  
  .tooltip {
    position: absolute;
    padding: 12px;
    background: rgba(15, 20, 40, 0.9);
    border: 1px solid rgba(80, 200, 255, 0.5);
    border-radius: 8px;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.2s;
    font-size: 14px;
    z-index: 1000;
    color: #e0e0ff;
    box-shadow: 0 0 10px rgba(80, 200, 255, 0.3);
    max-width: 250px;
    white-space: normal;
    word-wrap: break-word;
  }
  
  /* Smart tooltip positioning */
  .tooltip.right-edge {
    transform: translateX(-100%);
    margin-left: -20px;
  }
  
  .tooltip.bottom-edge {
    transform: translateY(-100%);
    margin-top: -20px;
  }
  
  .state {
    transition: fill 0.3s, stroke 0.3s;
    stroke: rgba(80, 200, 255, 0.3);
    stroke-width: 0.5;
  }
  
  .state:hover {
    stroke: rgba(80, 200, 255, 0.8);
    stroke-width: 1.5;
    filter: drop-shadow(0 0 5px rgba(80, 200, 255, 0.5));
  }
  
  .ufo-sighting {
    transition: r 0.3s, fill 0.3s;
    filter: drop-shadow(0 0 3px rgba(255, 215, 0, 0.5));
  }
  
  .ufo-sighting:hover {
    filter: drop-shadow(0 0 8px rgba(255, 215, 0, 0.8));
  }
  
  .military-base {
    transition: fill 0.3s;
    filter: drop-shadow(0 0 3px rgba(220, 20, 60, 0.5));
  }
  
  .military-base:hover {
    filter: drop-shadow(0 0 8px rgba(220, 20, 60, 0.8));
  }

  .nuclear-reactor {
    transition: r 0.3s, fill 0.3s;
    filter: drop-shadow(0 0 5px rgba(50, 205, 50, 0.5));
  }
  
  .nuclear-reactor:hover {
    cursor: pointer;
    filter: drop-shadow(0 0 10px rgba(50, 205, 50, 0.8));
  }
  
  .reactor-buffer {
    pointer-events: none;
  }
  
  /* UFO-themed map styling */
  .map-container text {
    fill: #c0c0ff;
    font-family: 'Exo 2', sans-serif;
  }
  
  .map-container .domain {
    stroke: rgba(80, 200, 255, 0.5);
  }
  
  .map-container .tick line {
    stroke: rgba(80, 200, 255, 0.3);
  }
  
  .map-container .tick text {
    fill: #c0c0ff;
  }